function valuetext(value: number) {
  return `${value}Â°C`
}

class VerticalSlider extends StreamlitComponentBase {
  public render = (): ReactNode => {
    return (
      <Box sx={{ height: 100 }}>
        {/* <ThemeProvider theme={theme}> */}
          <Slider
            sx={{
              '& input[type="range"]': {
                WebkitAppearance: "slider-vertical",
              },
              // "& .MuiSlider-thumb": {
              //   borderRadius: "3px",
              // },
              margin: 10,
            }}
            className="slider"
            orientation="vertical"
            defaultValue={this.props.args["value"]}
            getAriaValueText={valuetext}
            valueLabelDisplay="on"
            step={this.props.args["step"]}
            color="primary"
            // marks={marks}
            min={this.props.args["min"]}
            max={this.props.args["max"]}
            onChange={this.onChange}
          />
        {/* </ThemeProvider> */}
      </Box>
    )
  }


public onChange = (event: any) => {
    Streamlit.setComponentValue(event.target.value)
  }
}


